@import "/www/css/service";

.client_company_item {
    @extend %ui-header-text;

    align-items: center;
    background-color: $dark-blue;
    display: flex;
    flex-direction: row;
    font-size: 20px;
    gap: 0 8px;
    justify-content: space-between;
    padding: 12px 8px;

    &.client_company_item__disabled {
        opacity: 0.5;
        pointer-events: none;
    }
}

.client_company_item__left,
.client_company_item__right {
    align-items: center;
    display: flex;
    flex-direction: row;
    gap: 0 8px;
    justify-content: flex-start;
}

.client_company_item__left {
    overflow: hidden;
    padding-left: 4px;
}

.client_company_item__right {
    justify-content: flex-end;
}

.client_company_item__map_name {
    @extend %ellipsis;
    @extend %ui-header-text;

    background-color: transparent;
    border: 0;
    cursor: pointer;
    display: block;
    margin: 0;
    padding: 0;
}
