.map_preview {
    border: 2px solid #666;
    box-shadow: 0 0 2px 2px #ddd;
    display: block;
    position: relative;
}

.map_preview_image {
    display: block;
    image-rendering: pixelated;
    left: 0;
    max-width: 100%;
    position: absolute;
    top: 0;
}

.map_preview_image__frame {
    display: block;
    max-width: 100%;
}
