@import "/www/css/var";

.landscape_layer {
    display: block;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
}

.landscape_water_layer {
    display: block;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
}

.landscape_water_layer__square {
    height: $square-size;
    overflow: hidden;
    position: absolute;
    width: $square-size;
    z-index: 1;
}

.landscape_layer_image {
    display: block;
    height: 100%;
    image-rendering: pixelated;
    position: relative;
    width: 100%;
    z-index: 2;
}
