@import '/www/css/service';
@import '/www/css/var';

.faced_snackbar__content {
    @extend %ui-border-1;
    @extend %popup-size;

    align-items: flex-start;
    animation-timing-function: ease-in-out;
    background-origin: border-box;
    bottom: calc($bottom-status-bar-size / 2);
    box-shadow: 4px 4px 0 0 #000a;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    gap: 0 8px;
    justify-content: flex-start;
    left: 50%;
    opacity: 0;
    padding: 0 4px;
    position: fixed;
    transform: translate3d(-50%, 200%, 0);
    z-index: $mame-snackbar-overlay-z-index;
}

.faced_snackbar__markdown_item {
    @extend %ui-header-text;

    &.faced_snackbar__markdown_item__no_image {
        align-items: stretch;
        display: flex;
        flex-direction: column;
        justify-content: center;
        min-height: 38px;
        text-align: center;
    }

    p:last-child {
        margin-bottom: 0;
    }
}

.faced_snackbar__content_wrapper {
    display: block;
    flex-grow: 1;
}

.faced_snackbar__face {
    display: block;
    float: left;
    margin-top: -48px;
    padding-bottom: 4px;
    padding-right: 16px;
}
